import hasAnswersForActivity from '../logic/hasAnswersForActivity';
import isEditorFullScreen from '../logic/isEditorFullScreen';

function disableCheckAnswersButton($scope, rcfService) {
	if (!hasAnswersForActivity($scope)) {
		return true;
	}

	if (isEditorFullScreen($scope)) {
		return true;
	}

	switch ($scope.gradableType) {
		case 'open-gradable':
		case 'non-gradable':
		case 'open-non-gradable':
		case 'answer-key':
		case undefined:
			return true;

		case 'item-based':
			return rcfService.getItemPointsAvailable($scope.activityItemBasedItemId) === 0;

		default:
			return false;
	}
}

export default disableCheckAnswersButton;
