const searchActivitiesFactory = ($resource) =>
	$resource(
		`api.php/activity/find/:level/:searchValue`,
		{
			level: '@level',
			searchValue: `@searchValue`
		},
		{
			get: {
				method: `GET`,
				url: `api.php/activity/find/:level/:searchValue`,
				isArray: true
			}
		}
	);

export default searchActivitiesFactory;
