import activityFactory from './activityFactory';
import adminSettingsFactory from './adminSettingsFactory';
import breadcrumbRestFactory from './breadcrumbRestFactory';
import levelImagesFactory from './levelImagesFactory';
import metadataFactory from './metadataFactory';
import rawMetadataFactory from './rawMetadataFactory';
import searchActivitesFactory from './searchActivitiesFactory';
import sectionMetadataFactory from './sectionMetadataFactory';
import tableOfContentsFactory from './tableOfContentsFactory';

const setupRestServices = (angular) => {
	angular
		.module('rest.services', ['ngResource'])
		.factory('Activities', activityFactory)
		.factory('AdminSettings', adminSettingsFactory)
		.factory('BreadcrumbRestFactory', breadcrumbRestFactory)
		.factory('LevelImages', levelImagesFactory)
		.factory('Metadata', metadataFactory)
		.factory('RawMetadata', rawMetadataFactory)
		.factory('SearchActivities', searchActivitesFactory)
		.factory('SectionMetadata', sectionMetadataFactory)
		.factory('TableOfContents', tableOfContentsFactory);
};

export default setupRestServices;
