const tableOfContentsFactory = ($resource) =>
	$resource(
		`api.php/toc/:path`,
		{
			path: `@path`
		},
		{
			get: {
				method: `GET`,
				isArray: true,
				transformResponse(data) {
					try {
						return JSON.parse(data);
					} catch (ex) {
						alert(
							`Error in data for the table of contents factory! \n\n${data} \n\n${ex}`
						);
						throw ex;
					}
				}
			}
		}
	);

export default tableOfContentsFactory;
