// directives
//
// angular directive to load RCF html and 'compile' it so that
// the RCF framework can interact with it
//
import rcfBreadcrumbsDirective from './breadcrumbs/breadcrumbsDirective';
import rcfNavArrowsDirective from './navarrows/navArrowsDirective';
import rcfTocDirective from './toc/tocDirective';
import rcfToolbarDirective from './toolbar/toolbarDirective';
import rcfActivityToolbar from './activityToolbar';
import rcfFeedbackContainer from './feedbackContainer';
import rcfDeveloperToolbar from './developerToolbar';
import rcfPlayerToolbar from './playerToolbar';
import rcfJsonErrors from './jsonErrors';
import rcfViewerSidebar from './viewerSidebar';
import rcfScoreCard from './scoreCard';
import rcfReferenceContentToolbar from './referenceContentToolbar';

import onlyNumbers from './metadata/onlyNumbers';
import onlyNumbersAndRomanNumerals from './metadata/onlyNumbersAndRomanNumerals';
import onlyIntegers from './metadata/onlyIntegers';
import stringToNumber from './metadata/stringToNumber';

const setupDirectives = (angular) => {
	angular
		.module('rcf.viewer.directives', ['rcf.viewer.services'])
		.directive('rcfBreadcrumbs', rcfBreadcrumbsDirective)
		.directive('rcfNavarrows', rcfNavArrowsDirective)
		.directive('rcfToc', rcfTocDirective)
		.directive('rcfToolbar', rcfToolbarDirective)
		.directive('rcfActivityToolbar', rcfActivityToolbar)
		.directive('rcfFeedbackContainer', rcfFeedbackContainer)
		.directive('rcfDeveloperToolbar', rcfDeveloperToolbar)
		.directive('rcfPlayerToolbar', rcfPlayerToolbar)
		.directive('rcfJsonErrors', rcfJsonErrors)
		.directive('rcfReferenceContentToolbar', rcfReferenceContentToolbar)
		.directive('rcfViewerSidebar', rcfViewerSidebar)
		.directive('rcfScoreCard', rcfScoreCard)

		.directive('onlyIntegers', onlyIntegers)
		.directive('onlyNumbers', onlyNumbers)
		.directive('onlyNumbersAndRomanNumerals', onlyNumbersAndRomanNumerals)
		.directive('stringToNumber', stringToNumber);
};

export default setupDirectives;
