import isEditorFullScreen from '../logic/isEditorFullScreen';
import hasAnswersForActivity from '../logic/hasAnswersForActivity';
import isActivityItemBased from '../logic/isActivityItemBased';

const disableNextAnswerButton = ($scope, rcfService) => {
	// return false if no scope activity jsonContent exists

	if (!hasAnswersForActivity($scope)) {
		return true;
	}

	if (isEditorFullScreen($scope)) {
		return true;
	}

	if (isActivityItemBased($scope)) {
		return true;
	}

	const numberOfOneByOneAnswers = rcfService.getNumberOfOneByOneAnswers(
		JSON.parse($scope.activity.jsonContent)
	);

	// only show if > 1 answer available
	return numberOfOneByOneAnswers < 2;
};

export default disableNextAnswerButton;
