/* configure routing */
import configureRoutes from './routes/routes';

/* controllers */
import AdminController from './controllers/admin/adminController';
import FindController from './controllers/find/findController';
import ImageDialogController from './controllers/imageDialog/imageDialogController';
import EditPanelController from './controllers/editPanel/editPanelController';
import MetadataController from './controllers/metadata/metadataController';
import MecMetadataController from './controllers/metadata/mecMetadataController';
import MecMetadataNewsController from './controllers/metadata/mecMetadataNewsController';
import ModalMessageController from './controllers/modalMessage/modalMessageController';
import PageController from './controllers/pageController/pageController';
import GradebookController from './controllers/gradebook/gradebookController';
import ProjectController from './controllers/ProjectController';
import ToolbarController from './controllers/toolbar/toolbarController';

/* filters... still used ? */
import imageFileNameFilter from './filters/imageFileNameFilter';

/* services */
import FeedbackService from './services/feedback/feedbackService';
import MecMetadataService from './services/mecServices/mecMetadataService';
import MecNewsMetadataService from './services/mecServices/mecNewsMetadataService';

/*  separate viewer app dependencies */
import setupDirectives from './directives/directives';
import setupRestServices from './services/rest/restServices';
import setupViewerFactories from './services/viewerServices/viewerFactories';

setupRestServices(angular);
setupViewerFactories(angular);
setupDirectives(angular);

angular.module('ViewerApp', [
	'ngRoute',
	'ui.ace',
	'rcf.viewer.services',
	'rest.services',
	'rcf.viewer.directives'
]);

angular
	.module('ViewerApp')
	// configure the routes
	.config(configureRoutes)
	// add any filters
	.filter('ImageFileNameFilter', imageFileNameFilter)
	// setup services (factories are added elsewhere)
	.service('FeedbackService', FeedbackService)
	.service('MecMetadataService', MecMetadataService)
	.service('NewsMetadataService', MecNewsMetadataService)
	// setup controllers
	.controller('AdminController', AdminController)
	.controller('GradebookController', GradebookController)
	.controller('EditPanelController', EditPanelController)
	.controller('FindController', FindController)
	.controller('ImageDialogController', ImageDialogController)
	.controller('MetadataController', MetadataController)
	.controller('MecMetadataController', MecMetadataController)
	.controller('MecMetadataNewsController', MecMetadataNewsController)
	.controller('ModalMessageController', ModalMessageController)
	.controller('ToolbarController', ToolbarController)
	.controller('ProjectController', ProjectController)
	.controller('PageController', PageController);
