import hasAnswersForActivity from '../logic/hasAnswersForActivity';
import isEditorFullScreen from '../logic/isEditorFullScreen';
import isActivityItemBased from '../logic/isActivityItemBased';

const disableTryAgainButton = ($scope) => {
	if (!hasAnswersForActivity($scope)) {
		return true;
	}

	if (isEditorFullScreen($scope)) {
		return true;
	}

	// do not show if it's an item based activity
	if (isActivityItemBased($scope)) {
		return true;
	}

	return !$scope.layout.marking;
};

export default disableTryAgainButton;
