import configureLocationProvider from './configureLocationProvider';
import createConfigurationFactory from './createConfigurationFactory';
import breadcrumbsFactory from './breadcrumbsFactory';
import dialogFactory from './dialogFactory';
import rcfFactory from './rcfFactory';
import rcfUtilsFactory from './rcfUtilsFactory';

const setupViewerFactories = (angular) => {
	angular
		.module('rcf.viewer.services', [])

		.config(configureLocationProvider)

		.factory('ConfigService', createConfigurationFactory)
		.factory('Breadcrumbs', breadcrumbsFactory)
		.factory('DialogFactory', dialogFactory)
		.factory('RcfService', rcfFactory)
		.factory('RcfUtils', rcfUtilsFactory);
};

export default setupViewerFactories;
