import initialiseRcfWithLevel from './initialiseRcfWithLevel';

const initialiseController = (
	$scope,
	$rootScope,
	ConfigService,
	rcfUtils,
	Metadata,
	FeedbackService,
	rcfService,
	AdminSettings
) => {
	$scope.layout.isDevMode = ConfigService.runningAsDeveloper();
	$scope.RCFVersion = rcfUtils.getVersion();

	const levelMatch = window.location.hash.match(/\/L\w+/g);

	if (levelMatch) {
		const level = levelMatch[0].replace('/L', 'Level_');
		initialiseRcfWithLevel(
			level,
			Metadata,
			$scope,
			$rootScope,
			ConfigService,
			FeedbackService,
			rcfService,
			rcfUtils
		);
	}

	AdminSettings.get({}, (adminData) => {
		$scope.adminSettings = adminData;
	});
};

export default initialiseController;
